"use client"

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardHeader, CardTitle, CardContent, CardFooter } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";

type QuizQuestion = {
  id: string;
  question: string;
  options: string[];
  correctAnswer: string;
  explanation: string;
};

const sampleQuestions: QuizQuestion[] = [
  {
    id: "q1",
    question: "Which anatomical structure is primarily responsible for weight-bearing in the lower extremity?",
    options: ["Femur", "Tibia", "Fibula", "Patella"],
    correctAnswer: "Femur",
    explanation: "The femur is the longest and strongest bone in the body, designed to bear the full weight of the body during standing and walking."
  },
  {
    id: "q2",
    question: "What is the most critical assessment in the immediate post-operative period for orthopaedic patients?",
    options: ["Pain level", "Neurovascular status", "Wound drainage", "Mobility level"],
    correctAnswer: "Neurovascular status",
    explanation: "Neurovascular assessment is critical to detect complications like compartment syndrome, which can lead to permanent damage if not identified early."
  },
  {
    id: "q3",
    question: "Which intervention is most effective for preventing deep vein thrombosis in orthopaedic patients?",
    options: ["Bed rest", "Early mobilization", "Compression stockings only", "Pain medication"],
    correctAnswer: "Early mobilization",
    explanation: "Early mobilization, when medically appropriate, is the most effective intervention for preventing DVT by promoting blood circulation."
  },
  {
    id: "q4",
    question: "What is the primary goal of pain management in orthopaedic nursing?",
    options: ["Complete pain elimination", "Functional improvement", "Medication compliance", "Patient satisfaction"],
    correctAnswer: "Functional improvement",
    explanation: "The primary goal is to manage pain to a level that allows for functional improvement and participation in rehabilitation activities."
  },
  {
    id: "q5",
    question: "Which sign indicates potential infection at a surgical site?",
    options: ["Mild swelling", "Increased warmth and redness", "Clear drainage", "Decreased pain"],
    correctAnswer: "Increased warmth and redness",
    explanation: "Increased warmth, redness, and swelling are classic signs of inflammation and potential infection that require immediate attention."
  }
];

const Quiz = () => {
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [selectedAnswers, setSelectedAnswers] = useState<Record<string, string>>({});
  const [showResults, setShowResults] = useState(false);
  const [error, setError] = useState<string>("");

  const currentQuestion = sampleQuestions[currentQuestionIndex];
  const totalQuestions = sampleQuestions.length;
  const progress = ((currentQuestionIndex + 1) / totalQuestions) * 100;

  const handleAnswerSelect = (answer: string) => {
    setSelectedAnswers(prev => ({
      ...prev,
      [currentQuestion.id]: answer
    }));
    setError("");
  };

  const handleNext = () => {
    if (!selectedAnswers[currentQuestion.id]) {
      setError("Please select an answer before proceeding.");
      return;
    }

    if (currentQuestionIndex < totalQuestions - 1) {
      setCurrentQuestionIndex(prev => prev + 1);
    } else {
      setShowResults(true);
    }
    setError("");
  };

  const handlePrevious = () => {
    if (currentQuestionIndex > 0) {
      setCurrentQuestionIndex(prev => prev - 1);
    }
    setError("");
  };

  const calculateScore = () => {
    let correct = 0;
    sampleQuestions.forEach(question => {
      if (selectedAnswers[question.id] === question.correctAnswer) {
        correct++;
      }
    });
    return correct;
  };

  const resetQuiz = () => {
    setCurrentQuestionIndex(0);
    setSelectedAnswers({});
    setShowResults(false);
    setError("");
  };

  if (showResults) {
    const score = calculateScore();
    const percentage = Math.round((score / totalQuestions) * 100);
    
    return (
      <div className="max-w-4xl mx-auto space-y-6">
        <Card>
          <CardHeader className="text-center">
            <CardTitle className="text-2xl">Quiz Results</CardTitle>
          </CardHeader>
          <CardContent className="text-center space-y-6">
            <div className="text-6xl font-bold text-blue-600">
              {percentage}%
            </div>
            <p className="text-xl text-gray-600">
              You scored {score} out of {totalQuestions} questions correctly
            </p>
            
            <div className="text-left space-y-4 mt-8">
              <h3 className="text-lg font-semibold">Review Your Answers:</h3>
              {sampleQuestions.map((question, index) => {
                const userAnswer = selectedAnswers[question.id];
                const isCorrect = userAnswer === question.correctAnswer;
                
                return (
                  <div key={question.id} className="border rounded-lg p-4">
                    <p className="font-medium mb-2">
                      {index + 1}. {question.question}
                    </p>
                    <div className="space-y-1 text-sm">
                      <p className={`${isCorrect ? 'text-green-600' : 'text-red-600'}`}>
                        Your answer: {userAnswer}
                        {isCorrect ? ' ✓' : ' ✗'}
                      </p>
                      {!isCorrect && (
                        <p className="text-green-600">
                          Correct answer: {question.correctAnswer}
                        </p>
                      )}
                      <p className="text-gray-600 italic">
                        {question.explanation}
                      </p>
                    </div>
                  </div>
                );
              })}
            </div>
          </CardContent>
          <CardFooter className="justify-center">
            <Button onClick={resetQuiz} size="lg">
              Retake Quiz
            </Button>
          </CardFooter>
        </Card>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto space-y-6">
      <div className="text-center mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">
          Orthopaedic Nursing Knowledge Assessment
        </h1>
        <p className="text-gray-600">
          Test your understanding of key orthopaedic nursing concepts
        </p>
      </div>

      <Card>
        <CardHeader>
          <div className="flex justify-between items-center mb-4">
            <CardTitle className="text-lg">
              Question {currentQuestionIndex + 1} of {totalQuestions}
            </CardTitle>
            <span className="text-sm text-gray-500">
              {Math.round(progress)}% Complete
            </span>
          </div>
          <Progress value={progress} className="h-2" />
        </CardHeader>

        <CardContent className="space-y-6">
          <h2 className="text-xl font-medium leading-relaxed">
            {currentQuestion.question}
          </h2>

          <div className="space-y-3">
            {currentQuestion.options.map((option, index) => (
              <label
                key={index}
                className={`flex items-center space-x-3 p-4 border rounded-lg cursor-pointer transition-colors hover:bg-gray-50 ${
                  selectedAnswers[currentQuestion.id] === option
                    ? 'border-blue-500 bg-blue-50'
                    : 'border-gray-200'
                }`}
              >
                <input
                  type="radio"
                  name={`question-${currentQuestion.id}`}
                  value={option}
                  checked={selectedAnswers[currentQuestion.id] === option}
                  onChange={(e) => handleAnswerSelect(e.target.value)}
                  className="w-4 h-4 text-blue-600"
                />
                <span className="text-gray-900">{option}</span>
              </label>
            ))}
          </div>

          {error && (
            <div className="p-3 bg-red-50 border border-red-200 rounded-md">
              <p className="text-red-600 text-sm">{error}</p>
            </div>
          )}
        </CardContent>

        <CardFooter className="flex justify-between">
          <Button
            variant="outline"
            onClick={handlePrevious}
            disabled={currentQuestionIndex === 0}
          >
            Previous
          </Button>
          
          <Button onClick={handleNext}>
            {currentQuestionIndex === totalQuestions - 1 ? 'Finish Quiz' : 'Next Question'}
          </Button>
        </CardFooter>
      </Card>
    </div>
  );
};

export default Quiz;
